
.raido-holder input {
  display: none;
}
.raido-holder label {
  padding: 5px;
  border: none;
  background: #1f90d8;
  color: #fff;
  font-weight: bold;
  text-decoration: none;
  cursor: pointer;
  margin-bottom: 20px;
  border-radius: 4px;
  -webkit-transition: all .3s;
  transition: all .3s;
  border: 2px solid #000;
}
.raido-holder label:hover {
    background: #000;
}
.raido-holder input:checked + label {
  background: #000;
}



