
body {
  margin: 0;
  background: #eee;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 00px;
}
#app img {
  width: 250px;
}
.logo svg {
  -webkit-transition: all .4s;
  transition: all .4s;
}
.st0 {
  fill: #25475F;
}
.st1 {
  fill: #1F90D8;
  -webkit-transition: all .4s;
  transition: all .4s;
}
.logo {
  width: 190px;
  margin: 0 auto;
}
.logo:hover svg {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
}
.logo:hover .st0 {
    fill: #25475F;
}
.logo:hover .st1 {
    -webkit-box-shadow: 0 0 0 20px #25475F;
            box-shadow: 0 0 0 20px #25475F;
}
.icon_cancel1:before,
.icon_cancel:before {
  content: "\f00d" !important;
  display: inline-block;
  font: normal normal normal 14px/1 FontAwesome;
  font-size: inherit;
  text-rendering: auto;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}



